import React, {useEffect, useState} from "react";
import {Circle, MapContainer, Polyline, TileLayer, useMapEvents} from "react-leaflet";
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';

import Switch from '@mui/material/Switch';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import {RandomPath} from "./planner";
import Parse from 'parse/dist/parse.min.js';

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
    iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
    iconUrl: require('leaflet/dist/images/marker-icon.png'),
    shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});


const App = () => {
    const [stations, setStations] = useState([])
    const [cities, setCities] = useState([])
    const [states, setStates] = useState([])
    const [statuses, setStatuses] = useState({"stations": true, "cities": true, "states": true})
    const [startEnd, setStartEnd] = useState({"rank": true, "start": null, "end": null})

    useEffect(() => {

        const PARSE_APPLICATION_ID = 't1gxmiqaY7rLluwnd8qn79hMQPMSZoR4NAWPhbh1';
        const PARSE_HOST_URL = 'https://parseapi.back4app.com/';
        const PARSE_JAVASCRIPT_KEY = '88o1WbCKnCSuY2uCjotrPPIEnTWhUykDdJBZOO6Y';
        Parse.initialize(PARSE_APPLICATION_ID, PARSE_JAVASCRIPT_KEY);
        Parse.serverURL = PARSE_HOST_URL;

        fetchGet("stations").then(res => {
            setStations(res)
        })
        fetchGet("cities").then(res => {
            setCities(res)
        })
        fetchGet("states").then(res => {
            setStates(res)
        })


    }, [])

    async function fetchGet(table) {
        const query = new Parse.Query(table);
        return await query.find();
    }

    const DrawStations = () => {
        return stations.map((x, index) =>
            <Circle key={index} center={[x.attributes.latitude, x.attributes.longitude]}
                    pathOptions={{color: 'blue'}}></Circle>)
    }
    const DrawCities = () => {
        return cities.map((x, index) =>
            <Circle key={index} center={[x.attributes.lat, x.attributes.lon]} pathOptions={{color: 'red'}}></Circle>)
    }
    const DrawStates = () => {
        return states.map((x, index) =>
            <Circle key={index} center={[x.attributes.lat, x.attributes.lon]} pathOptions={{color: 'green'}}></Circle>)
    }

    const switch_handle = (event) => {
        setStatuses(prev => ({...prev, [event.target.id]: !statuses[event.target.id]}))
    }

    const DrawRandomPath = () => {
        const path = RandomPath(states)
        if (path) {
            return path.slice(0, 15).map((x, index) =>
                <Polyline key={index} positions={[path[index], path[index + 1]]}/>
            )
        }

    }

    const MyComponent = () => {
        const map = useMapEvents({
            click: (e) => {
                if (startEnd["rank"])
                    setStartEnd(prev => ({
                        ...prev, "rank": !startEnd["rank"],
                        "start": e.latlng
                    }))
                else
                    setStartEnd(prev => ({
                        ...prev, "rank": !startEnd["rank"],
                        "end": e.latlng
                    }))
            }
        });
        return null;
    }

    const DrawClick = () => {
        if (startEnd["start"] && !startEnd["end"])
            return <Circle index={"start"} center={startEnd["start"]} radius={10000} color={"red"}/>
        else if (startEnd["start"] && startEnd["end"])
            return [<Circle index={"start"} center={startEnd["start"]} radius={10000} color={"red"}/>,
                <Circle index={"end"} center={startEnd["end"]} radius={10000} color={"blue"}/>]
    }

    return (
        <div>
            <FormGroup row>
                <FormControlLabel
                    control={<Switch id={"stations"} onChange={switch_handle} defaultChecked color="info"/>}
                    label="Stations"/>
                <FormControlLabel
                    control={<Switch id={"cities"} onChange={switch_handle} defaultChecked color="error"/>}
                    label="Cities"/>
                <FormControlLabel
                    control={<Switch id={"states"} onChange={switch_handle} defaultChecked color="success"/>}
                    label="States"/>
            </FormGroup>
            <MapContainer eventHandlers={{}} center={[36, 36]} zoom={6} style={{height: '100vh', width: '100wh'}}>
                <TileLayer
                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                />
                {statuses["stations"] ? <DrawStations/> : ""}
                {statuses["cities"] ? <DrawCities/> : ""}
                {statuses["states"] ? <DrawStates/> : ""}
                <DrawRandomPath/>
                <MyComponent/>
                <DrawClick/>
            </MapContainer>
        </div>
    )
}

export default App;